/* normalize */
*, *:after, *:before {
  margin: 0;
  padding: 0; }

html, body {
  width: 100%; }

a {
  text-decoration: none; }

a:link, a:visited, a:hover, a:active, a:focus {
  text-decoration: none; }

img {
  border: 0; }

ul, li {
  list-style: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button, select, input, textarea {
  margin: 0;
  font-family: inherit;
  text-transform: none;
  -webkit-appearance: none;
  border: none;
  background: transparent; }

h1, h2, h3, h4, h5, h6, b, strong, th {
  font-weight: normal;
  font-size: inherit; }

fieldset {
  border: 0; }

::placeholder {
  color: #9cafbd;
  opacity: 1; }

:-ms-input-placeholder {
  color: #9cafbd; }

.hide {
  overflow: hidden;
  position: absolute;
  width: 0 !important;
  height: 0 !important;
  margin: 0 !important;
  padding: 0 !important;
  font-size: 0 !important; }

